  methods: {
    readBook() {
      var myJson = JSON.stringify(this.listOfCard);
      console.log(Object.keys(myJson));
    },
    byBook(index) {
      var pm1 = JSON.stringify(this.listOfBooks[index]);
      var pm2 = JSON.parse(pm1);
      /* console.log(pm2); */

      if (typeof Storage !== "undefined") {
        this.listOfCard.push({
          /* id: pm1.id,  */
          name: pm2.name,
          writer: pm2.writer,
          year: pm2.year,
          genre: pm2.genre,
          price: pm2.price
        });
        localStorage.setItem("listCard", JSON.stringify(this.listOfCard));
        
        var pm3 = JSON.parse(pm2.price);
        this.totalOfAmount += parseInt(pm3);
        /* console.log(Number.isInteger(pm3)); */
        localStorage.setItem("totalAmount", JSON.stringify((this.totalOfAmount)));
        this.vrBadge++;
        localStorage.setItem("vrBadgeLocal", JSON.stringify(this.vrBadge));
      }
    },
 
    removeBook(index) {
       var pm1 = JSON.stringify(this.listOfCard[index]);
       var pm2 = JSON.parse(pm1);

       if (typeof Storage !== "undefined") {
        this.listOfCard.splice(index, 1);
        localStorage.setItem("listCard", JSON.stringify(this.listOfCard));
        this.totalOfAmount -= pm2["price"];
        /* localStorage.setItem("listCard", JSON.stringify(this.listOfCard)); */
        localStorage.setItem("totalAmount", JSON.stringify(this.totalOfAmount));
        this.totalOfAmount = JSON.parse(localStorage.getItem("totalAmount"));
        this.vrBadge--;
        localStorage.setItem("vrBadgeLocal", JSON.stringify(this.vrBadge));
      } else {
        alert("error!");
      }
    },
    navigateToSignIn() {
      router.push({ name: "signin" });
    },
   /*  localStorageClear() {
      localStorage.clear();
    } */
  }
};
(function(document) {
  "use strict";

  var TableFilter = (function(myArray) {
    var search_input;

    function _onInputSearch(e) {
      search_input = e.target;
      var tables = document.getElementsByClassName(
        search_input.getAttribute("data-table")
      );
      myArray.forEach.call(tables, function(table) {
        myArray.forEach.call(table.tBodies, function(tbody) {
          myArray.forEach.call(tbody.rows, function(row) {
            var text_content = row.textContent.toLowerCase();
            var search_val = search_input.value.toLowerCase();
            row.style.display =
              text_content.indexOf(search_val) > -1 ? "" : "none";
          });
        });
      });
    }

    return {
      init: function() {
        var inputs = document.getElementsByClassName("search-input");
        myArray.forEach.call(inputs, function(input) {
          input.oninput = _onInputSearch;
        });
      }
    };
  })(Array.prototype);

  document.addEventListener("readystatechange", function() {
    if (document.readyState === "complete") {
      TableFilter.init();
    }
  });
})(document);

$(".dropdown-menu a.dropdown-toggle").on("click", function(e) {
  if (
    !$(this)
      .next()
      .hasClass("show")
  ) {
    $(this)
      .parents(".dropdown-menu")
      .first()
      .find(".show")
      .removeClass("show");
  }
  var $subMenu = $(this).next(".dropdown-menu");
  $subMenu.toggleClass("show");

  $(this)
    .parents("li.nav-item.dropdown.show")
    .on("hidden.bs.dropdown", function(e) {
      $(".dropdown-submenu .show").removeClass("show");
    });

  return false;
});
</script>
